import { Box, Button, Flex, SimpleGrid } from '@chakra-ui/react';
import { V4MAPPED } from 'dns';
import React from 'react';
import { buildDateArray, getFirstDayOfMonth, isLeapYear } from '../util/months';
import { CalanderDay } from './CalanderDay';
import { v4 } from 'uuid';

type Props = {
	month: string;
	year: number;
};

const CalanderContainer = (props: Props) => {
	const { month, year } = props;

	const firstDay = getFirstDayOfMonth(month, year);
	const dateArray = buildDateArray(firstDay, month, isLeapYear(year));

	return (
		<>
			<SimpleGrid
				mt={6}
				mb={6}
				mx={'auto'}
				border={'2px'}
				borderColor={'gray.400'}
				w='98%'
				columns={7}
			>
				{dateArray.map((date) => (
					<CalanderDay key={v4()} day={date} />
				))}
			</SimpleGrid>
		</>
	);
};

export { CalanderContainer };
