import { Box, Button, Flex } from '@chakra-ui/react';
import React, { useState } from 'react';
import { CalanderContainer } from './CalanderContainer';
import { months } from '../util/months';
import { SelectDate } from './SelectDate/SelectDate';

type Props = {};

const Calander = (props: Props) => {
	const date = new Date();

	const [month, setMonth] = useState(months[date.getMonth()]);
	const [year, setYear] = useState(date.getFullYear());

	const changeMonth = (month: string): void => {
		setMonth(month);
	};

	const changeYear = (year: number): void => {
		setYear(year);
	};

	return (
		<>
			<Flex h={'70px'}>
				<SelectDate
					year={year}
					months={months}
					changeYear={changeYear}
					changeMonth={changeMonth}
				/>
				<Box fontSize={25} mt={2} p={2}>
					{month}
				</Box>
				<Box fontSize={25} mt={2} p={2}>
					{year}
				</Box>
			</Flex>
			<Flex basis={'fit-content'} maxW={'99%'} h={'95vh'} m={'auto'}>
				<Box flex={1} border={'1px'}>
					Small Calander Selector
				</Box>
				<Box w={'90vw'} maxH={'80vw'} border={'1px'}>
					<Flex mt={4} gap={20} justify={'space-evenly'}>
						<Box>Sunday</Box>
						<Box>Monday</Box>
						<Box>Tuesday</Box>
						<Box>Wednesday</Box>
						<Box>Thursday</Box>
						<Box>Friday</Box>
						<Box>Saturday</Box>
					</Flex>
					<CalanderContainer month={month} year={year} />
				</Box>
			</Flex>
		</>
	);
};

export { Calander };
