export const months = [
	'January',
	'Febuary',
	'March',
	'April',
	'May',
	'June',
	'July',
	'August',
	'September',
	'October',
	'November',
	'December',
];

export const daysInMonth = (isLeapYear: boolean): { [key: string]: number } => {
	return {
		January: 31,
		Febuary: isLeapYear ? 28 : 29,
		March: 31,
		April: 30,
		May: 31,
		June: 30,
		July: 31,
		August: 31,
		September: 30,
		October: 31,
		November: 30,
		December: 31,
	};
};

export const getFirstDayOfMonth = (month: string, year: number) => {
	return new Date(year, months.indexOf(month), 1).getDay();
};

export const isLeapYear = (year: number): boolean => {
	if (year % 4 === 0) {
		return true;
	}
	return false;
};

export const getPrevMonth = (month: string): string => {
	return month === 'January' ? 'December' : months[months.indexOf(month) - 1];
};

export const buildDateArray = (
	firstDayOfWeek: number,
	month: string,
	isLeapYear: boolean
) => {
	const days = [];
	const prevMonth = getPrevMonth(month);
	const daysInPrevMonth = daysInMonth(isLeapYear)[prevMonth];
	console.log(daysInPrevMonth - firstDayOfWeek);
	for (
		let day = daysInPrevMonth - firstDayOfWeek;
		day < daysInPrevMonth;
		day++
	) {
		days.push(day);
	}
	for (let day = 1; day < daysInMonth(isLeapYear)[month]; day++) {
		days.push(day);
	}
	for (let day = 1; days.length < 42; day++) {
		days.push(day);
	}
	return days;
};
